a {
    color: var(--body-color);
    text-decoration: none;
    transition: color var(--transition);
    &:hover {
        color: var(--col-primary-400);
    }
}

a:not([class]) {
    font-weight: var(--fw-900);
}

a.noline {
    text-decoration: none;
}
a.anchor {
    display: block;
    position: relative;
    top: calc(var(--h-top) * -1); //-150px;
    visibility: hidden;
}

@media (min-width:992px) {
    .cols-lg-2 {
        columns: 2;
    }
    .cols-lg-3 {
        columns: 3;
    }
}

.cols-2 {
    columns: 2;
}
.cols-3 {
    columns: 3;
}

.mw-md-65 {
    @media (min-width:992px) {
        max-width: 65ch;
        // margin: auto;
    }
}

